@import "../../definitions";

.contextual-data {
  border-bottom: 1px solid $color-text-dividers;
  @include label-content-typeg($color-text-secondary);
  @include padding(15 , "top");
}

.contextual-data__list {
  border-bottom: 1px solid $color-text-dividers;
  margin: 0;
  @include padding(15 , "bottom");
  text-align: center;
}

.contextual-data__list_title {
  display: inline-block;
  @include label-content-typeg($color-text-secondary);
  margin: 0;
  padding: 0;
}

.contextual-data__list_desc {
  display: inline-block;
  @include label-content-typeg($color-text-secondary);
  margin: 0;
  @include padding(0 5 0 0);
}

.contextual-data__cite {
  display: none;
}

.contextual-data__doi {
  display: block;
  @include padding(15 0);
  text-align: center;
}

@media only screen and (min-width: #{get-rem-from-px(640)}rem) {
  .contextual-data {
    display: flex;
    @include padding(15 0);
  }

  .contextual-data__list {
    align-self: center;
    border-bottom: none;
    display: inline-block;
    padding: 0;
    text-align: left;
  }

  .contextual-data__list_desc {
    @include padding(0 10 0 0);
  }

  .contextual-data__cite_wrapper {
    float: right;
    margin-left: auto;
  }

  .contextual-data__cite {
    align-self: center;
    display: inline-block;
    flex: 1;
    text-align: right;
    @include padding(0 5 0 0);
  }

  .contextual-data__doi {
    display: inline-block;
    align-self: flex-end;
    padding: 0;
    text-align: right;
  }
}
